.s-footer__wvu--wrapper{
	width:100%;
	float:left;
	background:set-alpha(darken($black, 5), 0.55);
	margin-top:-3px;
	

  #s-footer__wvu{
  	background: $WVUBlue;
  	border-color:darken($primary,10);
  	border-top-color:lighten($primary,6);
  	box-shadow: 0 ms(-7) ms(-11) darken($primary, 10), 0 ms(-3) ms(-2) darken($black,10), 0 ms(-7) ms(-3) set-alpha(lighten($primary, 6), 0.5) inset;
  	border-style: solid;
  	border-width: ms(-11) 0 ms(-11) 0;
  	padding: 1rem 1.25% 0 1.25%;

	.container{
	  margin: 0 auto;
	  font-size:16px;
	  //background:url(data:image/jpeg;base64,) #005daa 54% top;
	  
  
	 .wvu-icons, .wvu-credits{
	 	@extend .font-Roman;

	 	p{
	 		@include adjust-font-size-to($font-size-small-2);
	 		color:$white;
	 		float:left;
	 		a{
	 			color:$WVUGold;
	 		}
	 	}
	   
	   a{
	   	color:shade($white,10);
	   	text-decoration: none;
	   	@include adjust-font-size-to($font-size-small-2);
	   	padding:1px 4px;
	   	border-bottom-style:solid;
        border-bottom-color:lighten($primary, 7);
        @include trailing-border(1px, $ms-7n/2);
        border-bottom-style:dotted;
	   	@include standard-transition(background-color);
        @include standard-transition(border);

        &:hover, &:focus{
            color:$white;
            background-color:tint($primary, 10); 
            border-top: 1px solid tint($WVUGold, 20);
            //border-bottom: 0;
        }
	   }

	    li{
	  	padding: 0 ms(-3);
	  	border-right: ms(-9) darken($primary, 3) solid;
	  	border-left: ms(-11) lighten($primary, 3) solid;
	  	margin:0;
	  	float:left;
	  	@include rhythm(0, 0, 0, $ms-3n, $font-size-small-2);
	  	
	  	&::before{
	  		content:none;
	  		padding-right:0;
	  	}
	  	&:first-child{
	  		padding-left:0;
	  		border-left:0;
	  	}
	  	&:last-child{
	  		padding-right:0;
	  		border-right:0;
	  	}
	  }//end li

	   .wvu-social li{
	   	border:0;
	   	padding: 0 ms(-5);

	   	a{
	   		border: 0; 
	   		&:hover, &:focus{
	   			border:0;
	   			background:transparent;
	   		}
	   	  }//end a
	   }//end wvu-social li

	 }//end .wvu-links, .wvu-credits
	  

	  
	}// end .container
  }//end #wvu-footer
}//end .wvu-footer-wrapper

